}

.Main {
  float: right;
  width: calc(100vw - 80px);
  padding-top: 50px;
  text-align: left;
}
.Main section {
  margin-bottom: 4em;
  padding: 0 1.5em;
}
.Main .title {
  display: inline-block;
  font-weight: 600;
  font-size: 1.7em;
}
.Main .mainVisual {
  width: 100%;
  height: 370px;
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-image: url(../../public/images/main/main_visual_bg.png);
  background-size: cover;
  background-position: 50% 50%;
}
.Main .mainVisual .univBox {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.Main .mainVisual .univBox .logo {
  width: 100px;
  height: 140px;
}
.Main .mainVisual .univBox .title {
  color: #fff;
  font-weight: 500;
  font-size: 1.2em;
  padding: 0.5em;
}
.Main .mainVisual .univBox .univ_btn {
  color: #fff;
  font-size: 1em;
  border: 1px solid #fff;
  background-color: rgba(0, 0, 0, 0.5);
}
.Main .mainVisual .aboutCamcoWrap {
  display: flex;
  width: 100%;
  padding: 1.5em 0em 0em;
  justify-content: flex-start;
  align-items: center;
}
.Main .mainVisual .aboutCamcoWrap .about_CamCo {
  position: relative;
  box-sizing: content-box;
  left: 0%;
  padding: 0.5em;
  background-color: #fff;
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.3s;
  pointer-events: auto;
  box-shadow: 0px 0px 5px 4px rgba(0, 0, 0, 0.2);
}
.Main .mainVisual .aboutCamcoWrap .about_CamCo:hover {
  background-color: #00a0e9;
}
.Main .mainVisual .aboutCamcoWrap .about_CamCo.on {
  background-color: #00a0e9;
}
.Main .mainVisual .aboutCamcoWrap .about_CamCo .CamCoImg {
  width: 1.7em;
  height: 1.7em;
  background-image: url(../../public/images/common/coachjob_logo.png);
  background-size: contain;
  background-position: 40% 50%;
  background-repeat: no-repeat;
  z-index: 20;
}
.Main .mainVisual .aboutCamcoWrap .about_CamCo .CamCoTxt {
  position: absolute;
  text-align: left;
  color: #fff;
  text-shadow: 1px 1px 7px rgba(0, 0, 0, 0.7);
  top: 50%;
  transform: translateY(-50%);
  font-size: 1em;
  left: calc(100% + 10px);
  width: 300px;
  transition: all 0.5s;
  overflow: hidden;
  white-space: nowrap;
  vertical-align: top;
  font-family: "Noto Sans KR Regular", "NanumGothic", "나눔고딕", sans-serif;
  opacity: 1;
}
.Main .mainVisual .aboutCamcoWrap .about_CamCo .CamCoTxt.two {
  word-break: break-all;
  transition: all 0.5s;
  opacity: 0;
}
.Main .mainVisual .aboutCamcoWrap .about_CamCo:hover .CamCoTxt.one {
  opacity: 0;
}
.Main .mainVisual .aboutCamcoWrap .about_CamCo:hover .CamCoTxt.two {
  opacity: 1;
}
.Main .mainVisual .aboutCamcoWrap .about_CamCo .click_flicker {
  color: #ffd54f;
  font-weight: 600;
  /* animation: ClickFlicker 1s alternate infinite; */
}
@-webkit-keyframes ClickFlicker {
  0% {
    color: #ffd54f;
  }
  100% {
    color: #fff;
  }
}
@keyframes ClickFlicker {
  0% {
    color: #ffd54f;
  }
  100% {
    color: #fff;
  }
}
.Main .mainVisual .aboutCamcoWrap .about_CamCo .CamCo_video {
  position: absolute;
  bottom: 20px;
  left: calc(100% + 10px);
  width: 0px;
  height: auto;
  transition: all 0.3s;
}
.Main .mainVisual .aboutCamcoWrap .about_CamCo.on .CamCo_video {
  background-color: #000;
  transition: all 0.3s;
  width: 500px;
  height: auto;
}
.Main .mainVisual .aboutCamcoWrap .about_CamCo:hover .CamCoTxt.two {
  opacity: 1;
}
